.nav{
    width: 40%;
}
.menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
    flex-wrap: wrap;
}

.menu li {
    height: 50px;
    transition: background-position-x 0.9s linear;
    text-align: center;
}
.menu li a {
    font-size: 15px;
    color: #fafafa;
    text-decoration: none;
    transition: all 0.45s;
}
.menu li:hover {
    animation: line 3s;
}
.menu li:hover a {
    color: #d94f5c;
}
.menu li:not(:last-child) {
    margin-right: 30px;
}

@-webkit-keyframes line {
    0% {
        background-position-x: 390px;
    }
}

@keyframes line {
    0% {
        background-position-x: 390px;
    }
}